import React, {Component} from "react";
import {champ_select, find_champ} from '../../../actions/index';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import ChampCont from './champ_cont';

class ChampionsNav extends Component {
    constructor(props) {
        super(props);
        this.state = {
            disp: ''
        };
      }
      handleClick(b) {
        this.setState({ disp_non: 'none' });
        this.setState({ disp_block: 'block' });
      }
      handleClick_re(b) {
        this.setState({ disp_block: 'none' });
        this.setState({ disp_non: 'flex' });
      }
    Champs () {
        const style_non = { display: this.state.disp_non };

        return this.props.champ_his.map ((champ) => {
            return(
            <div onClick={(b) => this.handleClick()} style={style_non} className="part_champ"  key={champ.id}>
                <div onClick={() => this.props.select (champ)} >
                    <img src={champ.img} alt=""/>
                    <div className="champ_name">
                    <div className="text_name">{champ.name}</div>
                        <div className="champ_more">подробнее</div>
                    </div>
                </div>
            </div>
            );
        });
    }
    findChamp(){
        this.props.find(this.searchInpute.value);
    }


    render(){
        const style_block = { display: this.state.disp_block };
        const style_non = { display: this.state.disp_non };
        const toInputUppercase = e => {
            e.target.value = ("" + e.target.value).toUpperCase();
          };

        return (
            <div>
                <div style={style_non} className="search">
                    <input type="text" spellCheck="false" onInput={toInputUppercase} placeholder="ИСКАТЬ ЧЕМПИОНА" onChange={this.findChamp.bind(this)} ref={(inpute) => { this.searchInpute = inpute }}/>

                </div>
                <div className="nav_champ" >
                    {this.Champs()}
                </div>

                <div style={style_block} className="his_cont">
                    <div onClick={ b => this.handleClick_re()} className="btn">Назад</div>
                    <ChampCont />
                </div>

            </div>
        );
    }
}

function mapStateToProps (state_champ) {
    return {
        champ_his: state_champ.champ_his.filter(champ => champ.name.includes(state_champ.filter))
    };
}

function matchDispatchToProps (dispatch) {
    return bindActionCreators({select: champ_select, find: find_champ}, dispatch)
}

export default connect(mapStateToProps, matchDispatchToProps)(ChampionsNav);