import React, {Component} from 'react';
import {connect} from 'react-redux';

class ChampCont extends Component {
    render () {
        if (!this.props.champ_ac) {
            return (<div></div>);
        }
        return (
            <div>
                <div className="heading">
                    <div>{this.props.champ_ac.name}</div>
                    <img src="img/history/t1HeaderDivider.png" alt=""/>
                </div>
                <div className="history">
                    <div>{this.props.champ_ac.story}</div>
                </div>
            </div>
        );
    }
}


function mapStateToProps (state_his) {
    return {
        champ_ac: state_his.champ_ac
    };
}

export default connect (mapStateToProps)(ChampCont);