import React from "react";

const Footer = () => (
    <div>
        &copy; inLoL <NowYear />
    </div>
);
class NowYear extends React.Component {
    Year = () => {
        let y = new Date();
        let yy = y.getFullYear();
        return (yy);
    }
    render() {
        return this.Year ();
    }
}
export default Footer;