import React, {Component} from "react";
import {part_select} from '../../../actions/index';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import HistortCont from './history_cont'

class HistoryNav extends Component {
    constructor(props) {
        super(props);
        this.state = {
            disp: ''
        };
      }
      handleClick(e) {
        this.setState({ disp_non: 'none' });
        this.setState({ disp_block: 'block' });
      }
      handleClick_re(e) {
        this.setState({ disp_block: 'none' });
        this.setState({ disp_non: 'flex' });
      }
    His () {
        return this.props.his.map ((part) => {
            return(
            <div className="part_his" onClick={() => this.props.select (part)} key={part.id}>
                <img src={part.img} alt=""/>
                <div className="part_name"><span>{part.name}</span></div>
            </div>
            );
        });
    }
    render() {
        const style_non = { display: this.state.disp_non };
        const style_block = { display: this.state.disp_block };
        return (
            <div>
                <div className="nav_his" onClick={ e => this.handleClick()} style={style_non}>
                    {this.His()}
                </div>
                <div style={style_block} className="his_cont">
                    <div onClick={ e => this.handleClick_re()} className="btn">Назад</div>
                    <HistortCont />
                </div>
            </div>
        );
    }
}

function mapStateToProps (state_his) {
    return {
        his: state_his.his
    };
}

function matchDispatchToProps (dispatch) {
    return bindActionCreators({select: part_select}, dispatch)
}

export default connect(mapStateToProps, matchDispatchToProps)(HistoryNav);