export const select = (category) => {
    return {
        type: "CATEGORY_SELECTOR",
        payload: category
    }
};
export const part_select = (part) => {
    return {
        type: "PART_SELECTOR",
        payload: part
    }
};
export const champ_select = (champ) => {
    return {
        type: "CHAMP_SELECTOR",
        payload: champ
    }
};
export const find_champ = (name) => {
    return {
        type: "FIND",
        payload: name
    }
};
