import {combineReducers} from 'redux';
import NavReducers from "./nav";
import ActiveCategoty from "./nav-active";
import ActivNavHis from './his-active';
import HistoryReducers from './history';
import ActiveNavChamps from './champ-active';
import ChampReducers from './champions';
import Find from './filter';


const allReducers = combineReducers ({
    nav: NavReducers,
    active: ActiveCategoty,
    his_ac: ActivNavHis,
    his: HistoryReducers,
    champ_ac: ActiveNavChamps,
    champ_his: ChampReducers,
    filter: Find
});

export default allReducers