import React, {Component} from 'react';
import {connect} from 'react-redux';
import FacePage from '../components/Pages/facePage';
import HistoryPage from './Pages/history/history_page';
import ChampionsPage from '../components/Pages/champions/champ_page';

class Content extends Component {

    Selector () {
            switch(this.props.nav_ac.id){
                case 1:
                    return (<HistoryPage />);
                case 2:
                    return (<ChampionsPage />);
                default:
                    return (<FacePage />);
        };
    }

    render () {
        if (!this.props.nav_ac) {
            return (<div><FacePage /></div>);
        }
        return (
            <div>
                {this.Selector ()}
            </div>
        );

    }
}

function mapStateToProps (state) {
    return {
        nav_ac: state.active
    };
}

export default connect (mapStateToProps)(Content);